.todo_container {
  width: 100%;
  padding: 2rem 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

.todo_title {
  width: 50rem;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.input_wrapper {
  display: flex;
  align-items: center;
  width: 50rem;
  height: 3rem;
  border-radius: 1rem;
  background-color: #fff;
  margin-bottom: 1rem;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
}

.todo_input {
  flex: 1;
  outline: none;
  border: none;
  padding-right: 1rem;
  padding-left: 1rem;
  font-size: 1rem;
  background-color: transparent;
  color: #111;
  height: 3rem;
}

.submit_button {
  width: 3rem;
  height: 3rem;
  background-color: #70d9d3;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  cursor: pointer;
  border-radius: 0 1rem 1rem 0;
}

.todo_list {
  width: 50rem;
  display: flex;
  flex-direction: column;
}

.empty {
  color: #777;
  font-size: 1.5rem;
  margin-top: 13rem;
}

@media screen and (max-width: 949px) {
  .todo_container {
    padding: 2rem;
  }

  .todo_title {
    width: 100%;
  }

  .input_wrapper {
    width: 100%;
  }

  .todo_list {
    width: 100%;
  }
}

